<template>
  <div>
    <group title="auto countdown">
      <cell title="15s" v-model="value">
        <countdown slot="value" v-model="time1" @on-finish="finish" v-show="show"></countdown>
      </cell>
    </group>
    <group title="manually">
      <x-switch title="start" v-model="start"></x-switch>
      <cell title="15s">
        <countdown slot="value" v-model="time2" :start="start" @on-finish="finish2"></countdown>
      </cell>
    </group>
  </div>
</template>

<script>
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'


export default {
  components: {
    Group,
    Cell,
    Countdown,
    XSwitch
  },
  methods: {
    finish (index) {
      this.show = false
      this.value = 'completed'
      console.log('current index', index)
    },
    finish2 (index) {
      this.start = false
      this.time = 20
    }
  },
  data () {
    return {
      show: true,
      time1: 15,
      time2: 15,
      value: '',
      start: false
    }
  }
}
</script>


